:root{
    --first-color: rgb(135,135,135);
    --second-color: rgb(97,97,97);
    --third-color: rgb(25,25,25);
    --white-color: rgba(247, 247, 247, 0.911);
}

.header{
    width:auto;
    display: flex;
    flex-direction: row;
    background-color: var(--third-color);
    justify-content: space-between;
    padding: 1rem;
    text-align: center;
    align-items: center;
}
.header-dark{
    background-color: var(--third-color);
}
.header-clear{
    background-color: rgb(241,243,245);
}

/* Logo */

.logo{
    /* color: var(--white-color); */
    text-decoration: none;
    font-weight: bold;
    cursor:pointer;
}

/* menu */
#menu-top{
    flex-grow: 5; 
}
#menu-top a{
    text-decoration: none;
    color: var(--first-color);
    margin-right: .5rem;
}
#menu-top a:active{
    color: var(--white-color);
}
.menu-separador{
    color: var(--second-color);
    margin-right: .4rem;
}
.active{
    color: var(--white-color)!important;
}
.active-dark{
    color: var(--thid-color);
    font-weight: bold;
}

.btn-burger{
    cursor: pointer;
}

.menu-all-screen{
    display:flex;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    background-color: black;
    opacity: 0.5;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.menu-all-screen a{
    color: white;
    display: block;
    font-size: 1.2rem;
    text-decoration: none;
}
.cerrar-menu-screen{
    color: white;
    font-size: 1.5rem;
    top: 1rem;    
    right:1.2rem;
    z-index: 1000;
    position: absolute;
}
.cerrar-menu-screen:hover{
    cursor: pointer;
}


/**/
.inicio-search{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-grow: 1;
}
/* boton search */
.color-search{
    color: var(--second-color);
}

/* inicio sesion*/
.color-inicio{
    color: var(--first-color);
    text-decoration: none;
    font-weight: bold;
    cursor:pointer;
}

#menu-min i{
    color: white;
    font-size: 1.5rem;
}
#menu-min{
    flex-direction: column;
    justify-content: center;    
}

@media (max-width: 560px){
    body{
        overflow-y: hidden;
    }
    #menu-top,#inicio-sesion,#boton-search{
        display: none;
    }
    #menu-min{
        display: flex;
    }
}
@media (min-width: 561px){
    body{
        overflow-y: scroll;
    }
    #menu-top,#inicio-sesion,#boton-search{
        display: inline-block;
    }
    #menu-min{
        display: none;
    }
}

.hidden{
    display: none;
}
.show{
    display: block;
}