.notes-section{
    width:90%;
    margin: auto;
    margin-top: 1.2rem;
    background-color: rgb(240, 240, 240);
}
.notes-section h3{
    margin-bottom:0;
    margin-top:0;
    text-align: left;
}
.titulo-nota{
    font-size: 1.5rem;
    color: rgb(43, 43, 43);
}

.imagen_nota{
    width:100%;
    height: 250px;
}

.div-autor-info{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    justify-items: center;
}

.div-autor-info h4{
    display: inline-block;
    margin-left:.3rem;
}

.autor-imagen{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.autor-imagen i{
    width:30px;
    height: 30px;
    background-color: rgb(172, 172, 172);
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 50%;
    align-items: center;
}

@media (max-width: 560px){
    .imagen_nota{
        height: 150px;
    }
    .descripcion-nota{
        display:none;
    }
}

.titulo-nota,.fecha-nota,
.descripcion-nota{
    margin-top:0.2rem;
    margin-bottom: 0;
}

.non-underline{
    text-decoration: none;
}
