body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(250, 250, 250);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* General */
.fondo-dark{
  background-color: rgb(58, 54, 54);;
}
.fondo-clear{
  background-color: white;
}
.texto-dark{
  color: black;
}
.texto-clear{
  color:white;
}
/* Fin General */

/** HOME */
.body-index{
  padding-top: 30px;
  padding-bottom: 30px;
}
.content-index{
  display: flex;
  flex-direction: column;
}
.home-section{
  display: flex;
  flex-direction: row;
  /* justify-content: space-between; */
  justify-content:space-evenly;
  flex-wrap: wrap;
  padding-left: 10px;
  padding-right: 10px;
}

@media (max-width: 560px) {
  .home-section{
    flex-direction: column;
  }
}

@media (min-width: 561px){
  .home-section{
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 0.1rem;
  }
}

@media (min-width: 1100px){
  .home-section{
      grid-template-columns: repeat(3, 1fr);
      width: 90%;
      margin: auto;
  }
}

@media (min-width: 1600px){
  .home-section{
      grid-template-columns: repeat(4, 1fr);
  }
}



/** FIN HOME */

/* ABOUT */
.page-about{
  display:flex;
  flex-direction: column;
  justify-content: space-between;
}
.about-goal{
  width:90%;
  margin:auto;
  flex-grow: 2;
}
.about-goal h2{
  font-size: 2rem;
}
.about-goal img{
  width: 30%;
  float:left;
  margin-right: 1rem;
}
.about-descripcion{
  font-size: 1.4rem;
  text-align: justify;
}
.thumbs-section{
  display: flex;
  flex-direction: row;
  align-items: center;
  width:20%;
  height:7rem;
  /* justify-content: space-between; */
  justify-content: space-evenly;
  margin:auto;
  margin-top: 20px;
}
.thumbs-section i{
  font-size: 1.9rem;
}
.thumbs-section i.like:hover{
  color:rgb(0, 132, 255);
  cursor: pointer;
}
.thumbs-section i.dislike:hover{
  color:rgb(255, 94, 0);
  cursor: pointer;
}
.thumbs-sub-section{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


/* FIN DE ABOUT*/

/* INICIO DE SESION */
.form-session section{
  background-color: var(--white-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 50vh;
}
.form-session form input{
  border: 3px solid black;
  width: 300px;
  height: 50px;
}
.form-session form div{
  margin-top: 10px;
}
.form-session form input::placeholder,
.form-session form input{
  font-size: 20px;
}
.form-session form input[type="text"]{
  color: gray;
}
.form-session form button{
  height: 50px;
  width: 310px;
  border: 3px solid gray;
  font-size: 20px;
  font-weight: bold;
}
.form-session h3{
  font-size: 1.9rem;
}
/* Fin Inicio de Sesion*/

/* Portafolio */
.div-subtitle{
  width:100%;
  height:100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.body-portfolio{
  flex-grow: 2; 
}

/* Blog Inicio */
.body-blog{
  width: 90%;
  margin: auto;
}
.list-blog-year aside{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}
.list-blog-year aside a{
  text-decoration: none;
}
.list-blog-year aside a:hover{
  text-decoration: underline;
}
.title-list-blog h2{
  border-bottom: 3px solid gray;
  color: rgb(114,191,141);
}
.div-list-blog{
  margin-bottom: 3rem;
}
.titulo-page-blog{
  width: 90%;
  margin: auto;
  font-size: 2rem;
}
/* Blog Fin */