.all-last-post{
    display: flex;
    flex-direction: column;
    margin: 30px;
}
@media(min-width: 1100px){
    .all-last-post{
        width:85%;
        margin:auto;
    }
}

.all-last-post h2{
    margin: 0;
    margin-bottom: .4rem;
    text-decoration: underline;
}

.last-post{
    /* margin: 30px; */
    /* display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 0.5rem;
}
.last-post-fecha{
    font-style: italic;
}
/* .last-post:nth-child(2){
    margin-left: .3rem;
} */
.image-last-post{
    width: 100%;
    height: 25vh;
}

.image-last-post img{
    width:100%;
    height: 100%;
}

.last-post h3{
    margin-top: 0px;
}

.btn-leer-mas{
    padding: .6rem .8rem;
    background-color: black;
    border-radius: 7%;
    color:white;
    text-decoration: none;
}

.btn-leer-mas:hover{
    background-color: white;
    color:black;
    border: 2px solid black;
}

.imagen-post{
    border-radius: 5%;
}