.icon-social-side {
    width: 100%;
}
.icon-social-side{
    margin-top:.9rem;
    display: flex;
    justify-content: space-evenly;
}
.icon-social-side i{
    font-size: 1.9rem;
}
.icon-social-side div:hover{
    cursor: pointer;
}
.icon-social-side .fa-facebook:hover{
    color: #3b5998;
}
.icon-social-side .fa-twitter:hover{
    color: #00acee;
}
.icon-social-side .fa-youtube:hover{
    color: #c4302b;
}
.icon-social-side .fa-telegram:hover{
    color:#0088cc;
}
.icon-social-side .fa-instagram:hover{
    color: #833AB4;
}
.icon-social-side .fa-github:hover{
    color: #171515;
}
.icon-social-side .fa-gitlab:hover{
    color: #fc6d26;
}
.icon-social-side .fa-linkedin:hover{
    color: #0e76a8;
}
dfn:hover:after{
    content: attr(title);
    background: #fff;
    padding: 5px 12px;
    border: solid 1px #ddd;
    position: absolute;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%);
}

.icons-clear i{
    color: white;
}
.icons-dark i{
    color: var(--third-color);
}
