:root{
    --titulo: rgba(255, 255, 255, 0.829);
    --votos-desc: rgba(179, 174, 174, 0.829);
}
.body-portafolio{
    padding-bottom: 30px;
}
.titulo-page-portafolio{
    margin: 0 30px;
    font-size: 2rem;
}

.tags-projects{
    display: flex;
    flex-direction: column;
    /* background-color: rgb(58, 54, 54); */
    background-color: rgb(33,37,41);
    margin: 30px;
    padding: 1rem;
}
.anio-votos-project{
    display: flex;
    justify-content: space-between;
    font-size: 1.1rem;
}
.anio-project{
    color: rgb(235, 161, 23);
    font-weight: bold;
}
.votos-project{
    color: var(--votos-desc);
    margin-right: 10px;
    font-weight: bold;
}
.votos-project ~ .far{
    color: rgb(235, 161, 23);
}
.titulo-project{
    color:var(--titulo);
    font-size: 1.3rem;
}
.desc-project{
    color: var(--votos-desc);
    font-size: 1.5rem;
    font-weight: 500;
}
.boton-project{
    display: flex;
    margin-top: 30px;
    margin-bottom: 20px;
}
.boton-project a{
    text-decoration: none;
    padding: 10px 20px;
    color: var(--votos-desc);
    color: white;
    border: 2px solid #eee;
    border-radius: 5%;
}
.boton-project a:hover{
    cursor: pointer;
}
.boton-project div:nth-child(2),.boton-project div:nth-child(3){
    margin-left: 10px;
}